class BurgerCard {
  constructor(image, name, description, rating) {
    this.image = require(`../images/${image}`);
    this.name = name;
    this.description = description;
    this.rating = rating;
  }

  /*createStarRating() {
    const ratingDiv = document.createElement('div');
    ratingDiv.className = 'card-rating';

    for (let i = 0; i < 5; i++) {
      const star = document.createElement('img');
      star.src = i < this.rating ? require('../images/star_filled.png') : require('../images/star_empty.png');
      star.alt = 'star';
      ratingDiv.appendChild(star);
    }

    return ratingDiv;
  }*/

  createCard() {
    const card = document.createElement('div');
    card.className = 'card';

    const image = document.createElement('img');
    image.src = this.image;
    image.alt = this.name;
    card.appendChild(image);

    const cardContent = document.createElement('div');
    cardContent.className = 'card-content';

    const title = document.createElement('div');
    title.className = 'card-title';
    title.textContent = this.name;
    cardContent.appendChild(title);

    const description = document.createElement('div');
    description.className = 'card-description';
    description.textContent = this.description;
    cardContent.appendChild(description);

    cardContent.appendChild(this.createStarRating());

    card.appendChild(cardContent);

   /* const heartIcon = document.createElement('img');
    heartIcon.src = require('../images/heart_icon.png');
    heartIcon.className = 'heart-icon';
    heartIcon.alt = 'heart icon';
    card.appendChild(heartIcon);*/

    return card;
  }
}

module.exports = BurgerCard;
