require('./styles.css');
const BurgerCard = require('./components/burger-card');

// Create a heading
const heading = document.createElement('h1');
heading.textContent = 'Welcome to Burger Paradise';
document.body.appendChild(heading);

// Create a search bar
const searchBar = document.createElement('input');
searchBar.type = 'text';
searchBar.placeholder = 'Search for a burger...';
document.body.appendChild(searchBar);

// Create the container for the cards
const cardsContainer = document.createElement('div');
cardsContainer.id = 'cards-container';
document.body.appendChild(cardsContainer);

// Function to render burgers
const renderBurgers = (burgers) => {
  cardsContainer.innerHTML = '';
  burgers.forEach(burger => {
    cardsContainer.appendChild(burger.createCard());
  });
};

// Initial burger data
const burgers = [
  new BurgerCard('burger1.png', 'Cheese Burger', 'A delicious classic burger with lettuce, tomato, and cheese.', 4),
  new BurgerCard('burger2.png', 'veggie Burger', 'A savory BBQ burger with bacon and special sauce.', 5),
  new BurgerCard('burger3.png', 'Fried chicken Burger', 'A healthy veggie burger made with fresh ingredients.', 3),
  new BurgerCard('burger4.png', 'Chicken Burger', 'A cheese lover\'s dream burger with extra cheese.', 5)
];

// Render all burgers initially
renderBurgers(burgers);

// Add search functionality
searchBar.addEventListener('input', (event) => {
  const searchTerm = event.target.value.toLowerCase();
  const filteredBurgers = burgers.filter(burger => 
    burger.name.toLowerCase().includes(searchTerm)
  );
  renderBurgers(filteredBurgers);
});
